<template>
<div>
    <h1 class="fw-bold title-page">Danh sách khách hàng</h1>
    <table class="table table-responsive">
        <thead>
            <tr class="table-dark">
                <th class="rounded-start" width="10">#</th>
                <th>Tên khách hàng</th>
                <th>Số điện thoại</th>
                <th>Email</th>
                <th>Ngày sinh</th>
                <th class="rounded-end"></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item,index) in customers" :key="item.customer_id">
                
                <td>{{ index+1 }}</td>
                <td><span @click="show(item)" class="text-primary cursor_hand">{{ item.customer_name }}</span></td>
                <td>{{ item.customer_phone }}</td>
                <td>{{ item.customer_email }}</td>
                <td>{{ item.customer_birthday | moment('DD/MM/YYYY') }}</td>
                <td>
                    <button class="btn btn-warning btn-sm" @click="show(item)">
                        <i class="fa-light fa-pen-to-square"></i>
                    </button>
                </td>
            </tr>
        </tbody>
    </table>
    <pagination
      v-bind:pagination="pagination"
      v-on:click.native="fetchAll(pagination.current_page)"
      :offset="4"
    ></pagination>
    <ModalCustomerAdd ref="modalCustomer"></ModalCustomerAdd>
</div>
</template>

<script>
import pagination from "@/extends/Paginate";
import ModalCustomerAdd from "@/extends/ModalCustomerAdd.vue"
import {
    mapGetters,
    mapActions
} from 'vuex';

export default {
    components:{
        ModalCustomerAdd,
        pagination
    },
    computed: {
        ...mapGetters('customers', ['customers','pagination']),
    },
    created() {
        this.fetchAll();

    },
    methods: {
        ...mapActions('customers', ['getCustomers']),
        show(customer){
            //
            let isUpdate = false;
            if(customer !=null){
                isUpdate = true;
            }
            this.$refs.modalCustomer.show(customer,isUpdate);
        },
        fetchAll(page) {
            this.$econtract.showLoading();
            this.getCustomers({pageSize:20, page : page}).then(() => {
               
                this.$econtract.hideLoading();
            });
        }
    }

}
</script>
