<template>
<div>
    <nav aria-label="Page navigation">
        <ul class="d-flex pagination justify-content-center mt-2">
            <li class="page-item" v-if="pagination.current_page > 1">
                <a href="#" class="page-link" aria-label="Previous" v-on:click.prevent="changePage(pagination.current_page - 1)">
                    <span aria-hidden="true">&laquo;</span>
                </a>
            </li>
            <li class="page-item" v-for="page in pagesNumber" :class="{ active: page == pagination.current_page }" :key="page">
                <a class="page-link" href="#" v-on:click.prevent="changePage(page)">{{
            page
          }}</a>
            </li>
            <li class="page-item">
                <a v-if="pagination.current_page < pagination.last_page" href="#" class="page-link" aria-label="Next" v-on:click.prevent="changePage(pagination.current_page + 1)">
                    <span aria-hidden="true">&raquo;</span>
                </a>
            </li>
        </ul>
    </nav>
</div>
</template>

<script>
export default {
    props: {
        pagination: {
            type: Object,
            required: true,
        },
        offset: {
            type: Number,
            default: 4,
        },
    },
    computed: {
        pagesNumber: function () {
            if (!this.pagination.to) {
                return [];
            }
            var from = this.pagination.current_page - this.offset;
            if (from < 1) {
                from = 1;
            }
            var to = from + this.offset * 2;
            if (to >= this.pagination.last_page) {
                to = this.pagination.last_page;
            }
            var pagesArray = [];
            for (from = 1; from <= to; from++) {
                pagesArray.push(from);
            }
            return pagesArray;
        },
    },
    methods: {
        changePage: function (page) {
            this.pagination.current_page = page;
        },
    },
};
</script>
