<template>
  <b-modal
    id="modal-add-customer"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="md"
    ref="modalDetail"
    ignore-enforce-focus-selector="span.select2-container"
    title="Thêm người liên hệ"
  >
    <div class="form-group">
      <label for="">Tên người liên hệ</label>
      <input
        type="text"
        v-model="Customer.customer_name"
        class="form-control"
        placeholder="Tên người liên hệ"
      />
    </div>
    <div class="form-group">
      <label for="">Số điện thoại người liên hệ</label>
      <input
        type="text"
        v-model="Customer.customer_phone"
        class="form-control"
        placeholder="Số điện thoại người liên hệ"
      />
    </div>
    <div class="form-group">
      <label for="">Email người liên hệ</label>
      <input
        type="text"
        v-model="Customer.customer_email"
        class="form-control"
        placeholder="Email người liên hệ"
      />
    </div>

    <div class="form-group">
      <label for="">Ngày sinh người liên hệ</label>
      <date-picker v-model="Customer.customer_birthday" :config="options"></date-picker>
    </div>
    <template #modal-footer>
      <button @click="saveCustomer" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('modal-add-customer')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import moment from "moment";
import axios from "axios";

export default {
  components: {
    datePicker,
  },
  data() {
    return {
      Customer: {
        customer_name: "",
        customer_phone: 0,
        customer_id: 0,
        customer_email: "",
        // customer_birthday: moment("01/01/1970"),
        customer_birthday: this.$econtract.formatDate(new Date()),
      },
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
      isUpdate: false,
    };
  },
  methods: {
    async saveCustomer() {
      if (this.isUpdate) {
        await axios.put(`/customers/update`, this.Customer).then((response) => {
          if (response.status == 200) {
            this.$toast.success("Cập nhật người liên hệ thành công");
            this.$emit("addCustomerSuccess", response.data);
            this.$bvModal.hide("modal-add-customer");
          }
        });
      } else {
        await axios.post(`/customers`, this.Customer).then((response) => {
          if (response.status == 200) {
            this.$toast.success("Thêm người liên hệ thành công");
            this.$emit("addCustomerSuccess", response.data);
            this.$bvModal.hide("modal-add-customer");
          }
        });
      }
    },
    show(customer = null, isUpdate = false) {
      if (isUpdate) {
        this.Customer = customer;
        this.Customer.customer_birthday = moment(customer.customer_birthday).format(
          "DD/MM/YYYY"
        );
      }
      this.isUpdate = isUpdate;
      this.$bvModal.show("modal-add-customer");
    },
  },
};
</script>
